import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.scss']
})
export class ChatComponent implements OnInit {

  pulsing: boolean;
  opened: boolean;

  constructor() {
    this.pulsing = true;
    this.opened = false;
  }

  ngOnInit() {
  }

  openChatWindow() {
    this.pulsing = false;
    this.opened = !this.opened;
  }

}



