import {Component, OnInit } from '@angular/core';
import {Router, NavigationStart, NavigationEnd} from '@angular/router';
import {Location, PopStateEvent} from '@angular/common';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';

import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';
import {CvGeneratorComponent} from "./cv-generator/cv-generator.component";


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  animations: [
    trigger('flyInOut', [
      state('in', style({transform: 'translateX(0)'})),
      transition('void => *', [
        style({transform: 'translateX(-100%)'}),
        animate(100)
      ]),
      transition('* => void', [
        animate(100, style({transform: 'translateX(100%)'}))
      ])
    ])
  ]
})
export class AppComponent implements OnInit {


  title = 'Fougeron';


  private lastUrl: string;
  private yScrollStack: number[] = [];

  constructor(private router: Router, private location: Location,private dialog: MatDialog) {
  }

  ngOnInit() {
    this.addReturnButtonHandler();
  }

  onDownloadClick() {
    let dialogRef = this.dialog.open(CvGeneratorComponent, {
      height: '200px',
      width: '300px',
      hasBackdrop:true,
      disableClose:false,
      autoFocus:true,
      data:{}
    });
  }


  addReturnButtonHandler() {
    // Save the last Url and scroll position in order to scroll back to the same location
    // if the user press return
    this.location.subscribe((ev: PopStateEvent) => {
      this.lastUrl = ev.url;
    });
    this.router.events.subscribe((ev: any) => {
      if (ev instanceof NavigationStart) {
        if (ev.url !== this.lastUrl) {
          this.yScrollStack.push(window.scrollY);
        }
      } else if (ev instanceof NavigationEnd) {
        if (ev.url === this.lastUrl) {
          this.lastUrl = undefined;
          window.scrollTo(0, this.yScrollStack.pop());
        } else {
          window.scrollTo(0, 0);
        }
      }
    });
  }
}



