import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';


@ Component({
  selector: 'app-timeline-entry',
  templateUrl: './timeline-entry.component.html',
  styleUrls: ['./timeline-entry.component.scss'],
  animations: [
    trigger('flyInOut', [
      state('left', style({transform: 'translateX(0)', opacity: 1})),
      state('right', style({transform: 'translateX(0)', opacity: 1})),
      transition('void => left', [
        style({
          opacity: 0,
          transform: 'translateX(-100%)'
        }),
        animate('0.2s ease-in')
      ]),
      transition('void => right', [
        style({
          opacity: 0,
          transform: 'translateX(100%)'
        }),
        animate('0.2s ease-in')
      ])
    ]),
    trigger('fadeIn', [
      state('active', style({transform: 'translateX(0)', opacity: 1})),
      transition('void => active', [
        style({
          opacity: 0
        }),
        animate('0.2s ease-out')
      ])
    ])
  ]
})
export class TimelineEntryComponent implements OnInit {
  @Input('exp')
  exp: Experience;

  @Input('position')
  position: string;

  @Output() onSkillClick = new EventEmitter<string>();

  constructor() {
  }

  ngOnInit() {

  }

  clickSkillBadge(value) {
    this.onSkillClick.emit(value);
  }
}

export class Experience {
  id: string;
  name: string;
  location: string;
  logo:string;
  dateBegin: string;
  dateEnd: string;
  type: string;
  technologies: string[];
  longDescription: string;
  shortDescription: string;
  position: string;
  project: string;


  constructor() {
  }

  static sorter(e1:Experience, e2:Experience):number
  {

    if (e2.dateEnd === null || e1.dateBegin < e2.dateBegin) {
      return 1;
    } else if (e1.dateBegin === null || e1.dateBegin > e2.dateBegin) {
      return -1;
    }
    else {
      return 0;
    }
  }
}




