import {Component, OnInit} from '@angular/core';
import {Experience} from './timeline-entry/timeline-entry.component';
import {Apollo} from 'apollo-angular';
import gql from 'graphql-tag';
import {LoadableComponent} from "../../loadable/loadable.component";


@Component({
  selector: 'app-timeline',
  templateUrl: './timeline.component.html',
  styleUrls: ['./timeline.component.scss']
})
export class TimelineComponent extends LoadableComponent implements OnInit {

  experiences: Experience[];
  selectedSkills: string[] = [];

  constructor(private apollo: Apollo) {
    super();
  }

  ngOnInit() {
    this.apollo.watchQuery({query: getExperiences}).subscribe(({data}) => {
      this.setExperiences(data);
      this.loaded = true;
    });
  }

  displayedExperience(): Experience[] {
    let displayedExp: Experience[] = [];
    for (let i = 0; i < this.experiences.length; i++) {
      if (this.doDisplayExperience(this.experiences[i])) {
        displayedExp.push(this.experiences[i]);
      }
    }
    return displayedExp;
  }

  onSkillClick(value: string): void {
    if (this.selectedSkills.indexOf(value) < 0) {
      this.selectedSkills.push(value);
    }
  }

  deleteSkillFiler(skill: string): void {
    this.selectedSkills.splice(this.selectedSkills.indexOf(skill), 1);
  }

  doDisplayExperience(experience: Experience): boolean {
    return this.selectedSkills.length === 0 || this.selectedSkills.some(skill => experience.technologies.includes(skill));
  }


  setExperiences(data: any) {
    this.experiences = [];
    let allExperiences = [].concat(data.allExperiences);

    allExperiences.sort(Experience.sorter);

    for (let i = 0; i < allExperiences.length; i++) {
      let experience = new Experience();
      experience.name = allExperiences[i].establishment.name;
      experience.logo = allExperiences[i].establishment.logoUrl;
      experience.location = allExperiences[i].location;
      experience.dateBegin = this.formatDate(allExperiences[i].dateBegin);
      experience.dateEnd = allExperiences[i].dateEnd === null ? 'Maintenant' : this.formatDate(allExperiences[i].dateEnd);
      experience.type = allExperiences[i].type;
      experience.technologies = allExperiences[i].technologies.map(t => t.name);
      experience.longDescription = allExperiences[i].i18n[0].longDescription;
      experience.shortDescription = allExperiences[i].i18n[0].shortDescription;
      experience.position = allExperiences[i].i18n[0].position;
      experience.project = allExperiences[i].i18n[0].project;
      this.experiences.push(experience);
    }

  }

  formatDate(date: string): string {
    return new Date(date).toLocaleString("fr", {
      month: "short",
      year: "numeric"
    });
  }


}

const getExperiences = gql`
  query getExperiences {
  allExperiences  (orderBy:dateEnd_DESC){
    location
    dateBegin
    dateEnd
    technologies  {
      name
    }
    name
    establishment{
      name
      logoUrl
     }
    type 
    i18n : experienceTranslations(filter: {language: FR}) {
      id
      longDescription
      shortDescription
      position
      project
    }
  }
}
`;



