import { provideClient } from './client';
import {ApolloModule} from 'apollo-angular';
import 'hammerjs';

import { ParticlesModule } from 'angular-particle';
import {NgModule} from '@angular/core';
import {AppRoutingModule} from './app-routing.module';
import {MaterializeModule} from 'angular2-materialize';
import {BrowserModule} from '@angular/platform-browser';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {MAT_DIALOG_DEFAULT_OPTIONS, MatButtonModule, MatCheckboxModule, MatDialogModule} from '@angular/material';
import { HttpClientModule } from '@angular/common/http';
import { AngularSvgIconModule } from 'angular-svg-icon';

import {AppComponent} from './app.component';
import {HomeComponent} from './home/home.component';
import {AboutComponent} from './about/about.component';
import {SkillsComponent} from './skills/skills.component';
import {LoadableComponent} from './loadable/loadable.component';
import {TimelineComponent} from './about/timeline/timeline.component';
import {TimelineEntryComponent} from './about/timeline/timeline-entry/timeline-entry.component';
import {JobService} from './about/timeline/job-service';
import {ChatComponent} from './chat/chat.component';
import {ChatWindowComponent} from './chat/chat-window/chat-window.component';
import { CvGeneratorComponent } from './cv-generator/cv-generator.component';


@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    AboutComponent,
    SkillsComponent,
    LoadableComponent,
    TimelineComponent,
    TimelineEntryComponent,
    ChatComponent,
    ChatWindowComponent,
    CvGeneratorComponent
  ],
  imports: [
    MatDialogModule,
    HttpClientModule,
    AngularSvgIconModule,
    MatButtonModule,
    MatCheckboxModule,
    BrowserAnimationsModule,
    MaterializeModule,
    BrowserModule,
    AppRoutingModule,
    ParticlesModule,
    ApolloModule.forRoot(provideClient)
  ],
  providers: [JobService,{provide: MAT_DIALOG_DEFAULT_OPTIONS, useValue: {hasBackdrop: false}}],
  bootstrap: [AppComponent],
  entryComponents: [
    CvGeneratorComponent
  ]
})
export class AppModule {
}



